"use client";

import { useEffect, useState, useRef } from "react";
import { useParams } from "next/navigation";

export default function ViewerPage() {
  const params = useParams();
  const token = params.token as string;
  const [slideIndex, setSlideIndex] = useState(1);
  const [startTime, setStartTime] = useState(Date.now());
  const [presentationUrl, setPresentationUrl] = useState<string>('');
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const iframeRef = useRef<HTMLIFrameElement>(null);

  const getEmbedUrl = (url: string) => {
    if (!url) return '';
    if (!url.includes('docs.google.com/presentation')) return url;

    let base = url;
    if (url.includes('/edit')) {
      base = url.split('/edit')[0];
    } else if (url.includes('/embed')) {
      base = url.split('/embed')[0];
    }

    // Always return a clean embed URL with our tracking-friendly params
    return `${base}/embed?start=false&loop=false&delayms=3000`;
  };

  useEffect(() => {
    // Fetch Presentation Details
    setIsLoading(true);
    setError(null);
    fetch(`http://localhost:3001/api/stats/${token}`)
      .then(res => res.json())
      .then(data => {
        if (data.presentation) {
          setPresentationUrl(data.presentation.googleSlidesUrl);
        } else {
          setError("Apresentação não encontrada.");
        }
      })
      .catch(err => {
        console.error("Failed to fetch presentation", err);
        setError("Erro ao carregar a apresentação ou servidor offline.");
      })
      .finally(() => setIsLoading(false));
  }, [token]);

  // Tracking Logic
  useEffect(() => {
    if (!presentationUrl) return;

    // 1. Send Open Event
    fetch('http://localhost:3001/api/track/open', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ token, userAgent: navigator.userAgent })
    }).catch(err => console.error("Tracking Error:", err));

    // 2. Slide Tracking (Mock implementation via simple timer for demo)
    // Real implementation requires postMessage from Google Slides API if enabled, 
    // or we use a "Next" button in our UI to control the iframe (if possible) or just track total time.
    // For this prototype, we'll assume the user might click a "Done" button or we just track session duration.

    const interval = setInterval(() => {
      // Heartbeat or check focus
    }, 5000);

    return () => clearInterval(interval);
  }, [token, presentationUrl]);

  // Function to log slide stay duration when changing slides
  // detailed tracking requires blocking cross-origin or using the Embed API

  if (isLoading) {
    return (
      <div className="min-h-screen bg-brand-black flex flex-col items-center justify-center p-4">
        <div className="w-12 h-12 border-4 border-brand-neon border-t-transparent rounded-full animate-spin"></div>
        <p className="mt-4 text-brand-gray animate-pulse font-medium">Carregando apresentação...</p>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-brand-black flex flex-col items-center justify-center p-4 text-center">
        <div className="w-16 h-16 bg-red-500/10 rounded-full flex items-center justify-center mb-4 border border-red-500/20">
          <span className="text-2xl text-red-500 font-bold">!</span>
        </div>
        <h2 className="text-2xl font-bold text-white mb-2">Ops!</h2>
        <p className="text-brand-gray mb-6">{error}</p>
        <a href="/" className="px-8 py-3 rounded-xl bg-brand-neon text-black font-bold hover:shadow-glow transition-all">
          Voltar para o início
        </a>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-brand-black flex flex-col items-center justify-center p-4">
      <div className="w-full max-w-6xl aspect-video bg-brand-darkgray rounded-2xl overflow-hidden shadow-[0_0_50px_rgba(26,255,0,0.15)] border border-white/5 relative">
        {presentationUrl ? (
          <iframe
            ref={iframeRef}
            src={getEmbedUrl(presentationUrl)}
            frameBorder="0"
            width="100%"
            height="100%"
            allowFullScreen={true}
            allow="autoplay"
          ></iframe>
        ) : (
          <div className="flex items-center justify-center h-full text-brand-gray">
            Endereço da apresentação inválido.
          </div>
        )}
      </div>

      <div className="mt-8 flex gap-4 items-center px-6 py-2 rounded-full bg-brand-darkgray/50 border border-white/5 backdrop-blur-sm">
        <span className="text-brand-gray text-xs uppercase tracking-widest font-bold">Visualização Anônima</span>
        <div className="h-2 w-2 rounded-full bg-brand-neon animate-pulse shadow-[0_0_10px_#1AFF00]"></div>
      </div>
    </div>
  );
}
